.wrapper {
  padding: 2rem; }

.header {
  margin-bottom: 1.33333rem; }
  .header:after {
    content: "";
    display: table;
    clear: both; }

.typeTitle {
  float: left;
  font-size: 5rem;
  font-weight: 600;
  line-height: 1.2em; }

.typeText {
  line-height: 1.5rem; }

.score {
  float: right; }
  .score * {
    display: inline-block;
    padding: 0.33333rem;
    font-size: 1.16667rem;
    font-weight: 600;
    margin-top: 3.5rem;
    line-height: 1.2em; }



